import React from 'react';
import Tags from '../../UI/Tags/Tags';
import classNames from 'classnames';
import ThemeButton from '../../UI/ThemeButton/ThemeButton';
import './ProjectPreview.scss'

const projectPreview = (props) => {
    return (
        <div className={classNames("project-preview", {inverted: props.inverted})}>
            <div className="project-preview-content">
            <h2 className="project-preview-name">{props.project.title}</h2>
            <p className="project-preview-description">{props.project.description}</p>
            <div className="project-preview-tags">
                <Tags tags={props.project.tags} dark={props.inverted} searchIn="projects"></Tags>
            </div>
            <ThemeButton onClick={props.clicked}>View Project</ThemeButton>

            </div>
            <div className="project-preview-image"> 
                <img src={props.project.imageURL} alt=""></img>
            </div>
        </div>
    )
}

export default projectPreview;