import React from 'react';
import NavigationItem from './NavigationItem/NavigationItem';
import './Navigation.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faAddressBook, faIdCard, faBriefcase} from '@fortawesome/free-solid-svg-icons';


const navigation = (props) => {
    return (

        <nav className="nav">
 
            <li className="nav-item">
                <NavigationItem link='/' exact><FontAwesomeIcon icon={faIdCard}/></NavigationItem>
            </li>
            <li className="nav-item">
                <NavigationItem link='/projects'><FontAwesomeIcon icon={faBriefcase}/></NavigationItem>
            </li>
            {/* <li className="nav-item">
                <NavigationItem link='/blog'><FontAwesomeIcon icon={faBookOpen}/></NavigationItem>
            </li> */}
            <li className="nav-item">
                <NavigationItem link='/contacts'><FontAwesomeIcon icon={faAddressBook}/></NavigationItem>
            </li>
            
        </nav>
    );
}

export default navigation;