import React from 'react';
import ZzzIcon from '../../assets/icons/zzz.svg'

const Placeholder = (props) => (
  <div> 
 <object data={ZzzIcon} style={{maxWidth: "200px", width: "90%", margin: "40px auto", display: "block"}}> </object>
 <p style={{textTransform: "uppercase", color:"#533ca2", fontSize:"40px", textAlign:"center"}}>404</p>
 <p style={{textTransform: "uppercase", color:"#533ca2", fontSize:"20px", textAlign:"center"}}>На этой странице очень скучно</p>

  </div>
);

export default Placeholder;