import React, { Component } from 'react';
import classNames from 'classnames';

import './SlideDown.scss';

class SlideDown extends Component {
  state = {
    opened: true,
    contentInitHeight: null
  }

  componentDidMount () {
    const contentInitHeight = this.contentRef.clientHeight;
    this.setState({contentInitHeight});
    this.toggle();
  }

  createContentRef = (el) => this.contentRef = el;

  toggle = () => {
    const opened = !this.state.opened;
    this.setState({opened})
  }
  render () {

    const contentMaxHeight = this.state.opened ? this.state.contentInitHeight : 0; 
    return (
      <div className={classNames("slidedown",{"opened": this.state.opened})}>
        <button  className="slidedown-button" onClick={this.toggle}></button>
        <div 
          className="slidedown-content" 
          ref={this.createContentRef}
          style={{maxHeight: contentMaxHeight}}
          >
          {this.props.children}
        </div>
      </div>
    );
  }
}

export default SlideDown;