import React from 'react';
import photo from '../../assets/images/about/photo.jpg'
import './About.scss';
import Brain from '../Tests/Brain';
import WorkingIdentity from '../WorkingIdentity/WorkingIdentity';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMapMarkedAlt, faUniversity, faBriefcase } from '@fortawesome/free-solid-svg-icons';

const about = (props) => {
    return (
        <div className="page">
            <h1 className="page-title">Fedor Viatkin</h1>
            <h2 className="page-subtitle">Frontend Developer</h2>
            <div className="about-container">
            <div className="about-description">
                <div className="about-photo">
                    <img src={photo} alt="Fedor Viatkin"></img>
                </div>
                <ul className="about-list">
                    <li><FontAwesomeIcon className="about-list-icon" icon={faMapMarkedAlt} /> Санкт-Петербург </li>
                    <li><FontAwesomeIcon className="about-list-icon" icon={faUniversity} /> СПбПУ Петра Великого</li>
                    <li><FontAwesomeIcon className="about-list-icon" icon={faBriefcase} /> Freelance <span className="about-date">07.2017 - 02.2018</span></li>
                    <li><FontAwesomeIcon className="about-list-icon" icon={faBriefcase} /> Adcombo <span className="about-date">02.2018 - ...</span></li>
                </ul>
            </div>
            <Brain/>
            <WorkingIdentity/>
        </div>

            
        </div>
    );
}

export default about;