import React from 'react';
import { NavLink} from 'react-router-dom';
import './NavigationItem.scss';

const navigationItem = (props) => {
    return (
        <NavLink
            className="nav-link"
            activeClassName="active"
            to={props.link}
            exact={props.exact}>
            {props.children}
        </NavLink>
    );
}

export default navigationItem;