import React, { Fragment } from 'react';
import Tag from './Tag/Tag';

const tags = (props) => {

    const renderedTags = props.tags.map((tag, i) => <Tag key={i} dark={props.dark} searchIn={props.searchIn}>{tag}</Tag>)
   
    return (
        <Fragment>
         {renderedTags}
         </Fragment>
    );
}

export default tags;