import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


const ContactLink = (props) => (
<a href={props.href} className="contacts-link">
      <FontAwesomeIcon className="contacts-icon" icon={props.icon}/>
      <span className="contacts-title">{props.title}</span>
    </a>
);

export default ContactLink;