import React, { Component } from 'react';
import BrainIcon from './BrainIcon/BrainIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowCircleDown } from '@fortawesome/free-solid-svg-icons';


// import brainSVG from '../../assets/brains-str.svg';
import BrainSvg from './BrainSvg';
import reactSVG from '../../assets/dev icons/react.svg';
import vueSVG from '../../assets/dev icons/vue.svg';
import sassSVG from '../../assets/dev icons/sass.svg';
import gitSVG from '../../assets/dev icons/git.svg';
import reduxSVG from '../../assets/dev icons/redux.svg';
import webpackSVG from '../../assets/dev icons/webpack.svg';
import nodejsSVG from '../../assets/dev icons/nodejs.svg';
import mongodbSVG from '../../assets/dev icons/mongodb.svg';
import svgSVG from '../../assets/dev icons/svg.svg';
import htmlSVG from '../../assets/dev icons/html.svg';
import cssSVG from '../../assets/dev icons/css.svg';
import jsSVG from '../../assets/dev icons/js.svg';




import './Brain.scss';

class Brain extends Component  {

  getBrainRef = el => this._brainEl = el;

  state = {
    trigerPosition: 0,
    animated: false
  }
  componentDidMount(){
    window.addEventListener('scroll', this.handleScroll);
    const brainTopPosition = this._brainEl.offsetTop;
    const brainHeight = this._brainEl.clientHeight;
    const trigerPosition = brainTopPosition-brainHeight;
    console.log(trigerPosition);
    this.setState({trigerPosition});
  }

  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = (e) => {
    if(this.state.animated) return;
    let scrolled = window.pageYOffset || document.documentElement.scrollTop;
    if(scrolled > this.state.trigerPosition) {
      this.setState({animated: true});
    }
   
  }
  render () { 
    
    return(
      <div className={`brain ${this.state.animated ? "animated" : null}`} ref={this.getBrainRef}>
        <div className="brain-placeholder">SKILLS
        <p>
        <FontAwesomeIcon icon={faArrowCircleDown} />
          </p>
          </div>
        <BrainSvg />
        <BrainIcon name="react" img={reactSVG} title="React"/>
        <BrainIcon name="vue" img={vueSVG} title="Vue"/>
        <BrainIcon name="sass" img={sassSVG} title="Sass"/>
        <BrainIcon name="git" img={gitSVG} title="Git"/>
        <BrainIcon name="redux" img={reduxSVG} title="Redux"/>
        <BrainIcon name="webpack" img={webpackSVG} title="Webpack"/>
        <BrainIcon name="nodejs" img={nodejsSVG} title="NodeJS"/>
        <BrainIcon name="mongodb" img={mongodbSVG} title="MongoDB"/>
        <BrainIcon name="svg" img={svgSVG} title="SVG" />
        <BrainIcon name="html" img={htmlSVG} title="HTML5" />
        <BrainIcon name="css" img={cssSVG} title="CSS3" />
        <BrainIcon name="js" img={jsSVG} title="JavaScript" />
      </div>
)}};

export default Brain;