import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import Tags from '../../components/UI/Tags/Tags';
import queryString from 'query-string';


import ProjectPreview from '../../components/Projects/ProjectPreview/ProjectPreview';
// import Project from './Project/Project';
import  './Projects.scss';

class Projects extends Component {
    state = {
        pageTags: ['React', 'Canvas', 'WebAudio', 'Algorithms', 'Experiment'],
        loadedProjects: [
            {   
                id: 1,
                title: 'Muzik Vizualizer',
                description: 'Визуализация музыки на React с помощью Canvas и WebAudio API. Поддерживает загрузку пользоватльских треков, смену алгоритма визуализации "на лету"',
                tags: ['React', 'Canvas', 'WebAudio', 'Experiment'],
                imageURL: require('../../assets/images/project-preview/react-muzik.png'),
                link: "https://texonth.github.io/muzik",
                inverted: true
            },
            {   
                id: 2,
                title: 'Sortz Vizualizer',
                description: 'Визуализация алгоритмов сортировки массивов на React.',
                tags: ['React', 'Canvas', 'Algorithms','Experiment'],
                link: "https://texonth.github.io/sortz",
                imageURL: require('../../assets/images/project-preview/sorts.png'),
            }
        ]
    }

    selectProjectHandler = (projectLink) => {
        // this.props.history.push(`/projects/${projectId}`);
        window.open(projectLink)
    }
    render () {
        const tagSearch = queryString.parse(this.props.location.search).tag || null;
        let filteredProjects;
        if (tagSearch) {
            filteredProjects = this.state.loadedProjects.filter(proj => proj.tags.includes(tagSearch));
        } else {
            filteredProjects = this.state.loadedProjects;
        }
        const renderedProjectsPreview = filteredProjects.map(
            project => <ProjectPreview key={project.id} project={project} inverted={!!project.inverted} clicked={() => this.selectProjectHandler(project.link)}/>
            );
        return (
            <div className="page">
               <h1 className="page-title">Projects</h1>
                <h2 className="page-subtitle">My Favorites</h2>
                {/* <p className="page-description">Проекты, которыми мне интересно поделиться с миром на данный момент.</p> */}
                <div className="page-tags"><Tags tags={this.state.pageTags} searchIn='projects' dark></Tags></div>
            <div className="projects-wrapper">
                {renderedProjectsPreview}
            </div>     
            </div>  
        );
    }
}

export default Projects;