import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import queryString from 'query-string';


import classNames from 'classnames'
import './Tag.scss';

const tag = (props) => {
    console.log(props);
    const currentTag = queryString.parse(props.location.search).tag 
    return (
        <Link to={{pathname: `/${props.searchIn || 'search'}`, search: `tag=${props.children}`}} className={classNames('tag', {'tag-dark': props.dark}, {'active': currentTag===props.children})} >{props.children}</Link>
    );
}

export default withRouter(tag);